<template>
<div class="wrap">
  <el-form :inline="false" class="demo-form-inline">
    <el-form-item label="上周车源量">
      <el-input v-model="szcyl" placeholder="上周车源量"></el-input>
    </el-form-item>
    <el-form-item label="车源价格">
      <el-input v-model="cyjg" placeholder="车源价格"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最低价">
      <el-input v-model="szcyzdj" placeholder="上周车源最低价"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最高价">
      <el-input v-model="szcyzgj" placeholder="上周车源最高价"></el-input>
    </el-form-item>
    <el-form-item label="车辆日期">
      <el-date-picker
        v-model="clrq"
        value-format="yyyy-MM-dd"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="车辆里程">
      <el-input v-model="cllc" placeholder="车辆里程"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最低里程">
      <el-input v-model="szcyzdlc" placeholder="上周车源最低里程"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最高里程">
      <el-input v-model="szcyzglc" placeholder="上周车源最高里程"></el-input>
    </el-form-item>
    <el-form-item label="上周车源平均最低里程">
      <el-input v-model="szcypjzdlc" placeholder="上周车源平均最低里程"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">查询</el-button>
    </el-form-item>
  </el-form>
  <el-table
    v-if="tableData"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="cl"
      label="车龄"
      width="180">
    </el-table-column>
    <el-table-column
      prop="scoreA"
      label="得分A"
      width="180">
    </el-table-column>
    <el-table-column
      prop="scoreB"
      label="得分B">
    </el-table-column>
  </el-table>
</div>
</template>

<script>
import { Form, FormItem, Button, Input,Select,Option,DatePicker,Table,TableColumn} from 'element-ui';
export default {
  name:'typeA',
  components:{
      [Form.name]:Form,
      [FormItem.name]:FormItem,
      [Button.name]:Button,
      [Input.name]:Input,
      [Select.name]:Select,
      [Option.name]:Option,
      [DatePicker.name]:DatePicker,
      [Table.name]:Table,
      [TableColumn.name]:TableColumn,
  },
   data() {
      return {
        szcyl:'',//上周车源量
        cyjg:'',//车源价格
        szcyzdj:0,//上周车源最低价
        szcyzgj:0,//上周车源最高价
        clrq:[],//车辆日期
        cl:0,//车龄
        pjlc:0,//平均里程
        cllc:0,//车辆里程
        tableData:[],
        szcyzdlc:0,//上周车源最低里程
        szcyzglc:0,//上周车源最高里程
        szcypjzdlc:0,//上周车源平均最低里程
        scoreA:0,//得分A
        scoreB:0,//得分B
        scoreC:0,//得分C
      }
    },
    methods: {
      onSubmit() {
        this.cl=this.fn1(this.clrq[0],this.clrq[1])
        this.pjlc=this.cllc/this.cl
        this.scoreA = 100-(60/this.szcyl)*(this.cyjg-this.szcyzdj)/((this.szcyzgj-this.szcyzdj)/this.szcyl)
        this.scoreB = 100-(60/this.szcyl)*(this.pjlc-this.szcyzdlc)/((this.szcyzglc-this.szcyzdlc)/this.szcyl)
        console.log(this.scoreA,this.scoreB)
        this.tableData.push({'cl':this.cl},{'scoreA':this.scoreA},{'scoreB':this.scoreB})
      },
      fn1(sDate1, sDate2){
        let  aDate,  oDate1,  oDate2,  iDays
        aDate   =  sDate1.split("-")
        oDate1 = new Date(aDate[0] , aDate[1] ,aDate[2])
        aDate   =  sDate2.split("-")
        oDate2 = new Date(aDate[0] , aDate[1] , aDate[2])
        iDays   =  parseInt(Math.abs(oDate1  -  oDate2)  /  1000  /  60  /  60  /24)
        return  iDays
      }
    },
    computed:{
      // 车龄
      // cl:this.fn1(this.clrq[0],this.clrq[1])
    }
}
</script>

<style>

</style>



